<template lang="pug">
    .test__question 
        .test__question {{index+1}}. 
            vue-mathjax(:formula="formula").test__question-text
        ul.test__answers
            li.test__answer
                label.test__answer-text {{answerChars[0]}}) 
                    vue-mathjax(:formula='question.answerA')
                input(@change='checkAnswer' type='radio' :value='0' :name="index" v-model='testInput').test__input
            li.test__answer
                label.test__answer-text {{answerChars[1]}})
                    vue-mathjax(:formula='question.answerB')
                input(@change='checkAnswer' type='radio' :value='1' :name="index" v-model='testInput').test__input
            li.test__answer
                label.test__answer-text {{answerChars[2]}})
                    vue-mathjax(:formula='question.answerC')
                input(@change='checkAnswer' type='radio' :value='2' :name="index" v-model='testInput').test__input
            li.test__answer
                label.test__answer-text {{answerChars[3]}})
                    vue-mathjax(:formula='question.answerD')
                input(@change='checkAnswer' type='radio' :value='3' :name="index" v-model='testInput').test__input
            li.test__answer(v-if='test.numberAnswers>4')
                label.test__answer-text {{answerChars[4]}})
                    vue-mathjax(:formula='question.answerE')
                input(@change='checkAnswer' type='radio' :value='4' :name="index" v-model='testInput').test__input
            li.test__answer(v-if='test.numberAnswers>5')
                label.test__answer-text {{answerChars[5]}})
                    vue-mathjax(:formula='question.answerF')
                input(@change='checkAnswer' type='radio' :value='5' :name="index" v-model='testInput').test__input
</template>

<script>
import {VueMathjax} from 'vue-mathjax'
export default {
    components: {
        'vue-mathjax': VueMathjax
    },
    props: {
        test: Object,
        question: Object,
        index: Number 
    },
    data() {
        return {
            testInput: '',
            formula: this.question.content,
            answerChars: ['a', 'b', 'c', 'd', 'e', 'f']
        }
    },
    mounted() {
        let recaptchaScript = document.createElement('script')
        recaptchaScript.setAttribute('src', 'https://cdnjs.cloudflare.com/ajax/libs/mathjax/2.7.2/MathJax.js?config=TeX-AMS_HTML')
        document.head.appendChild(recaptchaScript)
    },
    methods: {
        checkAnswer() {
            const local = JSON.stringify({
                    index: this.index,
                    answer: this.testInput
                })
            localStorage.setItem(this.index, local) 
        }
    }
}
</script>
<style lang="scss">
.mjx-chtml{ font-size: 100% !important; }
.test__question {
    border-bottom: dotted rgba($color: black, $alpha: 0.4);
    margin-bottom: 2%;
}
.test__question-text {
    font-family: 'Alegreya Sans Extra Bold';
    padding: 1%;
}
.test__answer{
    padding: 1%;
    display: flex;
}
.test__answer-text {
    margin-right: 5%;
    width: 40%;
}
</style>